.App {
  text-align: center;
}

.nav-bar {
  font-family: "SOYOMapleBoldTTF";
  font-size: 200%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(69, 67, 63);
  width: 100%;
  height: 15vh;
  color: white;
  flex-direction: column;
}

.TimeText {
  font-family: "SOYOMapleBoldTTF";
  font-size: 70%;
}

.menu-element-emp {
  position: relative;
  display: inline-block;
  background: linear-gradient(to top, #55ffaa 50%, transparent 40%);
}

.menu-element {
  position: relative;
  display: inline-block;
  background-image: -webkit-linear-gradient(top , transparent, transparent 1.76em, #cfcfcf 0);
  background-size: 100% 1.85em;
}

.menu-panel {
  font-family: "SOYOMapleBoldTTF";
  font-size: 150%;
  text-align: center;
  line-height: 200%;
  /*background: linear-gradient(45deg, rgb(255, 247, 217), rgb(178, 255, 105));*/
  background-image: url("../public/paper.jpg");
  background-size: cover;
  width: 100%;
  padding-top: 5vh;
  height: 80vh;
  color: black;
}

div {
  white-space: pre-wrap;
}

@font-face {
  font-family: 'SOYOMapleBoldTTF';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/SOYOMapleBoldTTF.woff2') format('woff2');
  font-weight: 700;
  font-style: normal;
}

body {
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none
}